<script>
import { getDynamicList, getDynamicVideoList, getDynamicInterestList, concern, praise, enshrine } from '../api/getdata'
import store from '../vuex/store'
import { Toast, Loadmore } from 'mint-ui'
import Header from '../components/Header'
import BottomNav from '../components/BottomNav'
export default {
  name: 'Community',
  data () {
    return {
      showBigPics: false,
      interestAll: false,
      videoAll: false,
      itemAll: false,
      page: 1,
      active: 'pics',
      tabNavShow: true,
      img1: 'img1',
      img3: 'img3',
      picArea1: 'picArea1',
      picArea2: 'picArea2',
      tab: 1,
      items: [],
      videos: [],
      interests: [],
      bigImg: '',
      tablinks: [{
        link: '图文', isActive: true, tab: 'pics'
      }, {
        link: '视频', isActive: false, tab: 'videos'
      }, {
        link: '关注', isActive: false, tab: 'interests'
      }]
    }
  },
  components: {
    Header,
    BottomNav,
    'mt-loadmore': Loadmore
  },
  mounted () {
    this.getDataList()
    // this.getVideoList()
    // this.getInterests()
    this.isLogin = localStorage.getItem('userState')
  },
  watch: {
    active: function () {
      for (let i = 0; i < this.tablinks.length; i++) {
        this.tablinks[i].isActive = false
        if (this.active === this.tablinks[i].tab) {
          this.tablinks[i].isActive = true
        }
      }
    }
  },
  methods: {
    // 获取图文列表
    getDataList () {
      let dynamicList = getDynamicList({belongType: '', pageIndex: this.page, pageSize: 10})
      dynamicList.then(res => {
        // console.log(res.data)
        this.items = res.data.result
      }).catch(err => {
        console.log(err)
      })
    },
    // 获取视频列表
    getVideoList () {
      let dynamicVideoList = getDynamicVideoList({belongType: '', pageIndex: this.page, pageSize: 10})
      dynamicVideoList.then(res => {
        // console.log(res.data)
        this.videos = res.data.result
        // console.log(this.videos)
      }).catch(err => {
        console.log(err)
      })
    },
    // 获取关注列表
    getInterests () {
      let dynamicInterestList = getDynamicInterestList({belongType: '', pageIndex: this.page, pageSize: 10})
      dynamicInterestList.then(res => {
        // console.log(res.data)
        this.interests = res.data.result
      }).catch(err => {
        console.log(err)
      })
    },
    // 关注切换
    toggleConcern (items, index) {
      if (this.isLogin === 'isLogin') {
        if (items[index].fIntType === '0') {
          let data = concern({state: 1, fInterestedId: items[index].fCreatedUser})
          data.then(res => {
            if (res.status === '0') {
              Toast({
                message: '关注成功',
                duration: 1500
              })
              items[index].fIntType = '1'
            }
          })
        } else if (items[index].fIntType === '1') {
          let data = concern({state: 0, fInterestedId: items[index].fCreatedUser})
          data.then(res => {
            if (res.status === '0') {
              Toast({
                message: '已取消关注',
                duration: 1500
              })
              items[index].fIntType = '0'
            }
          })
        }
        return items[index].fIntType
      } else {
        Toast({
          message: '请先登录',
          duration: 2000
        })
      }
    },
    // 显示动态发布者详情
    showUserInfor (items, index) {
      store.commit('getUserId', items[index].fUserId)
      if (items[index].fUserType === 'M') {
        store.commit('getUserType', '0')
      } else if (items[index].fUserType === 'T') {
        store.commit('getUserType', '1')
      } else if (items[index].fUserType === 'C') {
        store.commit('getUserType', '2')
      } else if (items[index].fUserType === 'U') {
        store.commit('getUserType', '3')
      }
      this.$router.push('/home/community/users')
    },
    // 点赞
    dianzan (items, index) {
      // console.log(this.items[index].zanType)
      if (this.isLogin === 'isLogin') {
        if (items[index].zanType === '1') {
          let data = praise({state: 0, fMontId: items[index].fMontId})
          data.then(res => {
            if (res.status === '0') {
              items[index].zanType = '0'
              items[index].fZanCnt--
            }
          })
        } else if (items[index].zanType === '0') {
          let data = praise({state: 1, fMontId: items[index].fMontId})
          data.then(res => {
            if (res.status === '0') {
              items[index].zanType = '1'
              items[index].fZanCnt++
            }
          })
        }
        return items[index].zanType
      } else {
        Toast({
          message: '请先登录',
          duration: 2000
        })
      }
    },
    // 收藏
    collect (items, index) {
      console.log(items[index].favType)
      if (this.isLogin === 'isLogin') {
        if (items[index].favType === '1') {
          let data = enshrine({state: 0, fMontId: items[index].fMontId})
          data.then(res => {
            if (res.status === '0') {
              items[index].favType = '0'
              items[index].fFavoriteCnt--
            }
          })
        } else if (items[index].favType === '0') {
          let data = enshrine({state: 1, fMontId: items[index].fMontId})
          data.then(res => {
            if (res.status === '0') {
              items[index].favType = '1'
              items[index].fFavoriteCnt++
            }
          })
        }
        return items[index].favType
      } else {
        Toast({
          message: '请先登录',
          duration: 2000
        })
      }
    },
    toggleTab (index) {
      window.scrollTo(100, 0)
      for (var i = 0; i < this.tablinks.length; i++) {
        this.tablinks[i].isActive = false
        this.tablinks[index].isActive = true
        this.active = this.tablinks[index].tab
        if (this.tablinks[index].link === '图文') {
          this.getDataList()
        } else if (this.tablinks[index].link === '视频') {
          this.getVideoList()
        } else {
          this.getInterests()
        }
      }
    },
    // 评论
    comment (items, index) {
      store.commit('commentId', items[index].fMontId)
      store.commit('getCommentType', 'parentComment')
      this.$router.push('/home/community/comment')
    },
    loadTopItem () {
      this.page = 1
      this.getDataList()
      setTimeout(() => {
        this.$refs.loadmoreItem.onTopLoaded()
      }, 2000)
    },
    loadBottomItem () {
      this.page++
      this.getDataList()
      setTimeout(() => {
        this.$refs.loadmoreItem.onBottomLoaded()
      }, 2000)
    },
    loadTopVideo () {
      this.page = 1
      this.getVideoList()
      setTimeout(() => {
        this.$refs.loadmoreVideo.onTopLoaded()
      }, 2000)
    },
    loadBottomVideo () {
      this.page++
      this.getVideoList()
      setTimeout(() => {
        this.$refs.loadmoreVideo.onBottomLoaded()
      }, 2000)
    },
    loadTopInterest () {
      this.page = 1
      this.getInterests()
      setTimeout(() => {
        this.$refs.loadmoreInterest.onTopLoaded()
      }, 2000)
    },
    loadBottomInterest () {
      this.page++
      this.getInterests()
      setTimeout(() => {
        this.$refs.loadmoreInterest.onBottomLoaded()
      }, 2000)
    },
    isHaveMore (haveMore) {
      this.haveMore = true
      if (haveMore) {
        this.itemAll = false
      }
    },
    showBigPic (items, index, index1) {
      document.addEventListener('touchmove', this.bodyscroll, false)
      document.body.parentNode.style.overflow = 'hidden'
      this.showBigPics = true
      this.bigImg = this.items[index].medias[index1].media
    },
    bodyscroll (e) {
      e.preventDefault()
      e.stopPropagation()
    },
    close () {
      this.showBigPics = false
      document.removeEventListener('touchmove', this.bodyscroll, false)
      document.body.parentNode.style.overflow = 'auto'
    }
  }
}
</script>

