<template>
    <div class="header">
        <span class="headTitle" @click="toggleShow">
            <span id="headTitleMsg">全部</span>
            <span class="pulldown">
              <img src="../assets/img/pulldown.png">
            </span>
        </span>
        <span class="release" @click="release">
          <img src="../assets/img/edit.png">
        </span>
        <ul class="pullDownMenu" v-show="isShow">
            <li class="menuLink" :class="{ active: menu.isActive }" v-for="(menu, index) in menus" :key="index" @click="handletouchstart(index)">{{ menu.link }}</li>
        </ul>
        <div class="mask" v-show="hasMask" @click="toggleShow"></div>
    </div>
</template>

<script>
import { Toast } from 'mint-ui'
export default {
  name: 'Header',
  data () {
    return {
      hasMask: false,
      isShow: false,
      isLogin: '',
      menus: [{
        link: '全部', isActive: true
      }, {
        link: '学员', isActive: false
      }, {
        link: '教练', isActive: false
      }, {
        link: '拳馆', isActive: false
      }, {
        link: '哼哈团', isActive: false
      }]
    }
  },
  mounted () {
    this.isLogin = localStorage.getItem('userState')
  },
  methods: {
    toggleShow () {
      this.isShow = !this.isShow
      this.hasMask = !this.hasMask
    },
    release () {
      if (this.isLogin === 'isLogin') {
        this.$router.push('/home/community/release')
      } else {
        Toast({
          message: '请先登录',
          duration: 2000
        })
      }
    },
    handletouchstart (index) {
      this.isShow = !this.isShow
      this.hasMask = !this.hasMask
      document.getElementById('headTitleMsg').innerHTML = this.menus[index].link
      for (var i = 0; i < this.menus.length; i++) {
        this.menus[i].isActive = false
        this.menus[index].isActive = true
      }
    }
  }
}
</script>

<style scoped>
  .header {
    height: 1.5rem;
    width: 100%;
    position: fixed;
    top: 0;
    background: #fff;
    line-height: 1.5rem;
    font-size: 0.5rem;
    color: #333333;
    z-index: 2;
  }
  .release {
    width: 0.5rem;
    height: 0.5rem;
    display: flex;
    position: absolute;
    justify-content: center;
    right: 0.3rem;
    top: 0.5rem;
  }
  .release img {
    width: 100%;
    height: 100%;
    }
  .pulldown {
    display: flex;
    align-items: center;
    width: 0.24rem;
    height: 0.16rem;
    margin-left: 0.08rem;
  }
  .pulldown img {
    width: 100%;
    height: 100%;
  }
  .headTitle {
    width: 2rem;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
  }
  .pullDownMenu {
    display: block;
    list-style: none;
    background: #fff;
    position: absolute;
    left: calc(50% - 1.6875rem);
    top: 0.6rem;
    padding: 0 0.75rem;
    z-index: 3;
    border-radius: 0.4rem;
  }
  .menuLink {
    line-height: 1.1rem;
  }
  .active {
    color: #ffd200;
  }
  .mask {
    z-index: 2;
    position: absolute;
    top: -10rem;
    width: 100%;
    height: 2000%;
    background: #000000;
    opacity: .24;
  }
</style>



